.episodes :hover{
  transition: opacity 0.5s;
}
.episodes{
  padding: 0 5rem; 
}
.episodes li{
  cursor: pointer;
  float: left; 
  padding-right: 1rem; 
}

.clear{
  clear: left; 
  display: block; 
}

.heartbtn{
  position: absolute;
  left: 5%;
}

.insidebtn{
  border: 2px solid red;
  border-radius: 8px;
  display: flex;
  cursor: pointer;
  padding: 4px;
  background: rgb(224, 88, 88);
}

.insidebtn .favorite{
  /* height: 20px;
  width: 40px; */
  font-size: 21px;
  font-weight: 600;
  font-family: sans-serif;
  color: rgb(247, 207, 207);
}
